<template>
  <div class="container">
    <div class="row">
      <div class="col-md-4">
      </div>
      <div class="col-md-4">
        <form class="form-horizontal">
          <div class="form-title">
            <h5>{{$t("登 录")}}</h5>
          </div>
          <b-input-group size="md" prepend="<i class='fa fa-user fa-md'></i>">
            <b-form-input type="text" :placeholder="$t('用户名')"></b-form-input>
          </b-input-group>
          <br/>
          <b-input-group size="md" prepend="<i class='fa fa-lock fa-md'></i>">
            <b-form-input type="password" :placeholder="$t('密码')"></b-form-input>
          </b-input-group>
          <br/>
          <b-input-group size="md">
            <b-button size="md" class="form-control btn-success">
              {{$t("登 录")}}
            </b-button>
          </b-input-group>
        </form>
      </div>
      <div class="col-md-4">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Login'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
    margin-top: -5rem;
    margin-bottom: -4rem;
    height: 100%;
    width: 100%;
    max-width: 100%;
    padding-top: 10rem;
    padding-bottom: 4rem;
    background-color: #001f3f;
    color: #ffffff;
    animation-name: changebg;
    animation-duration:30s; /*变换时间*/
    animation-delay:0s; /*动画开始时间*/
    animation-iteration-count:infinite; /*下一周期循环播放*/
    animation-play-state:running; /*动画开始运行*/
    background-size: 100% auto;
  }
  @keyframes changebg {
    0%   {background-color:#001f3f;}
    10%  {background-color:#0074D9;}
    20%  {background-color:#7FDBFF;}
    30%  {background-color:#39CCCC;}
    40%  {background-color:#3D9970;}
    50%  {background-color:#2ECC40;}
    60%  {background-color:#01FF70;}
    70%  {background-color:#FFDC00;}
    80%  {background-color:#FF851B;}
    90%  {background-color:#FF4136;}
    100%  {background-color:#B10DC9;}
  }
  .input-group-text {
    width: 2.5rem;
  }
  form {
    padding: 3rem;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 5px;
  }
</style>



